@import "../../global.scss";

body{
        background: rgb(22, 22, 22);
        background-size: 400% 400%;
}

.palette{
    width: 100%;
    display: flex;
    text-shadow: 1px 1px 1px rgb(22, 22, 22);
    
    @include tablet {
        flex-direction: column;
    }

    .palette-box{
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        font-size: 20px;

        .color{
            width: 100%;
            height: 60vh;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            cursor: pointer;

            @include tablet{
                flex-direction: row;
                max-height: 14vh;
                
            }

            span{
                opacity: 0;
                font-size: 30px;
                color: white;
                font-weight: 600;
                transition: all 0.1s ease;
                text-shadow: 10px black;
                transition: all .5s;

            }

            .copy-text{
                opacity: 0;
                font-weight: 600;
                color: white;
                transition: all .5s;

            }


            .name{
                position: relative;
                top:20%;
                bottom: 0;
                font-weight: 500;

                @include tablet{
                    font-size: 20px;
                    top:0;
                }
            }

            &:hover{

                span{
                    opacity: 1;
                }

                .copy-text{
                    opacity: .6;

                }
            }

            @include tablet{
                span{
                    opacity: 1;
                }

                .copy-text{
                    flex: .9;

                }

            }
        }  
    }
}

.values{
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    
    @include tablet{
        position: absolute;
    }

    .color-code{
        width: 100%;
        display: flex;
        padding: 20px 20px 0px 20px;
        justify-content: center;
        align-items: center;
        gap: 10px;
        @include tablet{
        padding: 50px 20px 50px 20px;
        justify-content: left;
        margin-left: 10px;
        gap: 0px;
        }

    }
}

.code{
    min-width: 15%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
    span:nth-child(1){
        font-size: 20px;
        border-bottom: 1px solid;
        
    }
    span:nth-child(2){
        margin-top: 10px;
        font-weight: 500;
        font-size: 14px;
    }
}

