@import '../../global.scss';

.home{
    position: relative;
    height: 100%;
    @include tablet{
        overflow: hidden;
    }

    .title{
        height: 70px;
        display: flex;
        justify-content: center;
        align-items: center;
        color: white;

        @include tablet{
            display: none;
        }
    }

    .base-color{
        z-index: 9;
        position: sticky;
        position: -webkit-sticky;
        top:0px;
        height: 60px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 10px;
        transition: all .5s ease-in-out;
        
        @include tablet{
        top:0px;
        gap: 4px;

        }

        .selectors{

            @include mobile{
                width: 100px;
            }
        }
    }

    .mobile-btn{
        display: none;
        width:100%;
        height: 100%;
        padding: 10px;
        font-size: 30px;
        background-color: white;
        text-align: center;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        border: 0px;
        
        
        @include tablet{
            display: flex;
        }
    }

}