::-webkit-scrollbar{
    width: 0;
}

@mixin mobile {
  @media only screen and (max-width: 500px) {
    @content;
  }
}

@mixin tablet {
    @media only screen and (max-width: 768px) {
      @content;
    }
}
  
  @mixin desktop {
    @media only screen and (max-width: 992px) {
      @content;
    }
  }
  
  @mixin large {
    @media only screen and (max-width: 1200px) {
      @content;
    }
  }